<template>
  <div>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Teacher
      </el-col>
      <el-col :span="10">
        <el-select v-model="selectedTeacher" placeholder="请选择" @change="teacherChange">
          <el-option
            v-for="(item, index) in teachers"
            :key="item.objectId"
            :label="item.name"
            :value="index">
          </el-option>
        </el-select>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Title
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.title" placeholder="Title"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Maximum
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.maximum_num" placeholder="Maximum" type="number"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Fee
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.fee" placeholder="Fee"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Description
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.description" placeholder="Description" type="textarea" :rows="3"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Days
      </el-col>
      <el-col :span="10">
        <div>
          <el-checkbox label="Monday" v-model="days_check[0]"></el-checkbox>
          <el-checkbox label="Tuesday" v-model="days_check[1]"></el-checkbox>
          <el-checkbox label="Wednesday" v-model="days_check[2]"></el-checkbox>
          <el-checkbox label="Thursday" v-model="days_check[3]"></el-checkbox>
        </div>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Grade
      </el-col>
      <el-col :span="10">
        <div>
          <el-checkbox label="K" v-model="grade_check[0]"></el-checkbox>
          <el-checkbox label="1" v-model="grade_check[1]"></el-checkbox>
          <el-checkbox label="2" v-model="grade_check[2]"></el-checkbox>
          <el-checkbox label="3" v-model="grade_check[3]"></el-checkbox>
          <el-checkbox label="4" v-model="grade_check[4]"></el-checkbox>
          <el-checkbox label="5" v-model="grade_check[5]"></el-checkbox>
          <el-checkbox label="6" v-model="grade_check[6]"></el-checkbox>
          <el-checkbox label="7" v-model="grade_check[7]"></el-checkbox>
          <el-checkbox label="8" v-model="grade_check[8]"></el-checkbox>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Room
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.room" placeholder="Room"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="3">
        Remark
      </el-col>
      <el-col :span="10">
        <el-input v-model="course.remark" placeholder="Remark"></el-input>
      </el-col>
    </el-row>

    <el-row type="flex" justify="center" class="row" align="middle">
      <el-col :span="5">
        <el-button type="primary" @click="edit">Add</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {
  mapState
} from 'vuex'
import {add} from 'api/asa'
import {getTeacher} from 'api/user'
import {userValidation} from 'assets/js/mixin'
export default {
  name: 'HelloWorld',
  data () {
    return {
      teachers: [],
      selectedTeacher: 0,
      course: {
        title: '',
        maximum_num: 0,
        fee: 0,
        description: '',
        room: '',
        remark: ''
      },
      days_check: [false, false, false, false],
      grade_check: [false, false, false, false, false, false, false, false, false],
      editable: true
    }
  },
  computed: {
    ...mapState(['currentASA'])
  },
  mixins: [userValidation],
  created () {
    getTeacher().then((res) => {
      this.teachers = res
    })
  },
  methods: {
    teacherChange(value) {
      console.log(this.selectedTeacher)
    },
    edit() {
      if (this.editable) {
        this.editable = false
        let grade = ''
        for (let i in this.grade_check) {
          if (this.grade_check[i]) {
            if (i === '0') {
              grade += 'k'
            } else {
              grade += i
            }
          }
        }
        this.course.grade = grade
        let days = ''
        for (let i in this.days_check) {
          if (this.days_check[i]) {
            days += (parseInt(i) + 1)
          }
        }
        this.course.days = days
        if (this.course.title === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.maximum_num === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.fee === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.description === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.days === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.grade === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        if (this.course.room === '') {
          this.$notify({
            title: '提示',
            message: '信息有误，请从新填写'
          })
          this.editable = true
          return
        }
        this.editable = true
        this.course.fee = parseInt(this.course.fee)
        this.course.maximum_num = parseInt(this.course.maximum_num)
        this.course.teacher = {
          __type: 'Pointer',
          className: '_User',
          objectId: this.teachers[this.selectedTeacher].objectId
        }
        this.course.teacher_name = this.teachers[this.selectedTeacher].name
        this.course.submitted_student = 0
        this.course.session = {
          __type: 'Pointer',
          className: 'ASA_Control',
          objectId: this.$ls.get('control').objectId
        }
        add(this.course).then((res) => {
          this.$router.back()
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
  @import "~variables"
  .row
    margin-bottom $small-margin
</style>



