import Parse from 'parse'

export function login (data) {
  return Parse.Cloud.run('login', data)
}

export function test() {
  return Parse.Cloud.run('downloadPay', {
    email: 'jaygao@bcis.net.cn',
    subject: 'subject',
    body: 'body',
    filename: 'filename.csv'
  })
}

export function getTeacher () {
  return Parse.Cloud.run('getTeacher')
}



